/* Global styles */
.App {
  font-family: Arial, sans-serif;
  text-align: center;
}

/* Header styles */
.App-header, .App-footer {
  background-color: #282c34;
  color: white;
  padding: 20px;
  font-size: calc(10px + 2vmin);
}

/* Personal note and contact info styling */
.personal-note {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  color: #444;
  background: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  line-height: 1.6;
}

.contact-info {
  margin-top: 20px;
  font-weight: bold;
}

/* Carousel styles */
.carousel-root {
  margin: 20px auto;
  max-width: 80%; /* Adjusted width for desktop */
}

.carousel .slide {
  padding: 0 15px; /* Add space between slides */
  background: none;
}

.carousel .slide img {
  max-width: 100%;
  max-height: 250px; /* Adjust this value as needed */
  margin: 0 auto; /* Center the image */
}

.carousel .control-arrow,
.carousel.carousel-slider .control-arrow {
  filter: invert(100%);

}

/* App sections */
.app-section {
  margin: 40px 20px; /* top and bottom 40px, left and right 20px */
}


.app-section h2 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
  margin-left: auto; /* Align margin to the center */
  margin-right: auto; /* Align margin to the center */
  max-width: 90%; /* Limit the width to 90% of its container */
}

.app-section p {
  margin-left: auto; /* Align margin to the center */
  margin-right: auto; /* Align margin to the center */
  max-width: 90%; /* Limit the width to 90% of its container */
  margin-bottom: 35px;
}

@media (min-width: 1024px) {
  .carousel .slide img {
    max-height: 1000px; /* Larger images for larger screens */
  }
  .carousel .slide {
    padding: 0 20px; /* More space between slides */
  }
}

@media (max-width: 768px) {
  .personal-note, .app-section {
    max-width: 90%;
  }
  .carousel .slide img {
    max-height: 300px; /* Increase this value for larger images on medium screens */
  }
  .carousel-root {
    max-width: 90%; /* Adjusted width for mobile */
  }
}

@media (max-width: 600px) {
  .app-section {
    margin: 40px 0; /* top and bottom 40px, left and right 0 */
  }
  .app-section h2,
  .app-section p {
    padding: 0 5%; /* Add padding on small screens to create margins on the sides */
  }
  .personal-note, .carousel-root, .app-section {
    max-width: 100%; /* Full width for mobile screens */
    margin-left: auto;
    margin-right: auto;
  }
  .carousel .slide img {
    max-height: 450px; /* Adjust as needed */
  }
}

.ReactModal__Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 2000;
}

.ReactModal__Content {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  overflow: auto;
  z-index: 2001;
}

/* Ensuring the modal content is centered */
.ReactModal__Content--after-open {
  align-items: center;
  justify-content: center;
}

/* Style the close button */
.ReactModal__Content button {
  /* Add your button styles here */
}

.modal-image {
  display: block;
  max-width: 80%;
  max-height: 80vh;
  margin: 0 auto;
  object-fit: contain;
}

.modal-close-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  color: #333;
  font-size: 1.5rem;
}

/* Style adjustments for smaller screens */
@media (max-width: 600px) {
  .ReactModal__Content {
    padding: 20px;
    max-width: 90%;
  }
  .modal-image {
    max-height: 60vh;
  }
}